// src/services/sentiment.js

//        
const POSITIVE_WORDS = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

const NEGATIVE_WORDS = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

const ENERGY_WORDS = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

export const analyzeSentiment = (text) => {
  const words = text.toLowerCase().split(/\s+/);

  let positiveCount = 0;
  let negativeCount = 0;
  let energyCount = 0;

  words.forEach((word) => {
    if (POSITIVE_WORDS.includes(word)) positiveCount++;
    if (NEGATIVE_WORDS.includes(word)) negativeCount++;
    if (ENERGY_WORDS.includes(word)) energyCount++;
  });

  //   
  const sentiment =
    positiveCount > negativeCount
      ? "positive"
      : positiveCount < negativeCount
      ? "negative"
      : "neutral";

  //   
  const energyLevel =
    energyCount > 2 ? "high" : energyCount > 0 ? "medium" : "low";

  return {
    sentiment,
    energyLevel,
    stats: {
      positive: positiveCount,
      negative: negativeCount,
      energy: energyCount,
    },
  };
};

export const getSuggestions = (sentiment, energyLevel) => {
  const suggestions = {
    high_energy: {
      positive:
        " !      .",
      negative: "      .",
      neutral:
        "    -     .",
    },
    low_energy: {
      positive:
        " ,    .  .",
      negative: "    .  .",
      neutral: "    .",
    },
    medium_energy: {
      positive: "  -   .",
      negative: "     .",
      neutral: "   -     .",
    },
  };

  return suggestions[`${energyLevel}_energy`][sentiment];
};
